.cats {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, calc(20% - 32px));
  gap: 40px;
  overflow-y: visible;
}

.last-element {
  visibility: hidden;
  height: 1px;
  width: 100%;
}

@media screen and (max-width: 1000px) {
  .cats {
    grid-template-columns: repeat(4, calc(25% - 15px));
    gap: 20px;
  }
}

@media screen and (max-width: 700px) {
  .cats {
    grid-template-columns: repeat(3, calc(33.33% - 3.33px));
    gap: 5px;
  }
}