/* reset */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

img {
  vertical-align: top;
}

html,
body {
  height: 100%;
}

html {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
}

body {
  line-height: 1;
  font-family: 'Roboto';
}


/************** Base style ***************/

.app__content {
  /* Header + Margin */
  padding-top: calc(64px + 51px);
}

.container {
  box-sizing: border-box;
  padding: 0 12px;
  max-width: calc(1300px);
  margin: 0 auto;
}

@media screen and (max-width: 700px) {
  .app__content {
    padding-top: calc(64px + 20px);
  }
}